.produto{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.produto img{
  margin-bottom: 2rem;
}
.preco{
  padding: .5rem;
  background: #91fb5d;
  color: #017e01;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 4px;
}
.descricao{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2rem;
  line-height: 1.2;
}