.header{
  margin-bottom: 2rem;
}
.header ul{
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}
.header ul li{
  margin-right: 1rem;
}
.link{
  display: block;
  text-decoration: none;
  font-size: 1.2rem;
  color: black;
  background-color: #eee;
  padding: .5rem 1rem;
  border-radius: 4px;
}
.header :global(.active),
.link:hover{
  background-color: #ddd;
}